var pintuan_home_id = 0;
var countDown = 2;
export default {
	data() {
		return {
			pintuanData: '',
			action: 'self',
		}
	},
	onLoad(options) {
		console.log(options);
		var that = this;
		if (options.pintuan_orders_id) {
			this.$http.post("pintuan/oid2pid", {
				pintuan_orders_id: options.pintuan_orders_id
			}).then((res) => {
				that.pintuan_home_id = res.data.pintuan_home_id;
				that.getPintuan();
			})
		} else if (options.pintuan_home_id && options.action) {
			that.pintuan_home_id = options.pintuan_home_id;
			that.action = options.action;
			that.getPintuan();
		}
	},
	
	//监听用户下拉动作，一般用于下拉刷新
	onPullDownRefresh() {
		this.countDown = 1;
		this.getPintuan();
	},
	onHide() {
		this.countDown = 1;
	},
	onUnload() {
		this.countDown = 1;
	},
	onShow() {
		var that = this;
		this.countDown = 2;
		if (this.pintuanData != '' && this.countDown == 1) {
			this.getPintuan();
		} else {
			setTimeout(function() {
				that.setCountDown();
			}, 500)
		}

	},
	onShareAppMessage() {
		var that = this;
		var title = that.pintuanData.share_title;
		var path =
			"/pages/index/index?jump=pages/jointeam/jointeam&jumpData1=action&jumpData2=other&jumpData3=pintuan_home_id&jumpData4=" +
			that.pintuan_home_id;
		var imageUrl = that.pintuanData.goods_thumbnail_image;
		console.log(path);
		return {
			title: title,
			path: path,
			imageUrl: imageUrl,
		}
	},
	methods: {
		getPintuan() {
			var that = this;
			this.$http.post("pintuan/show", {
				pintuan_id: that.pintuan_home_id,
				action: that.action
			}).then((res) => {
				that.pintuanData = res.data;
				if (res.data.status > 1) {
					uni.setStorageSync('record_user_list', res.data.record_user_list);
					uni.setStorageSync('goods_name', res.data.goods_name);
					uni.redirectTo({
						url: "/pages/jointeam/join_full?pintuan_home_id=" + that.pintuan_home_id,
					})
					return;
				} else {
					setTimeout(function() {
						that.setCountDown();
					}, 500)
				}


			})
		},
		// 一个倒计时
		setCountDown() {
			var that = this;
			var time = 1000;
			var datas = this.pintuanData;
			if (datas.times <= 0) {
				datas.times = 0;
			}
			datas.times = datas.times * 1000;
			let formatTime = this.getFormat(datas.times);
			datas.times -= 1000;
			datas.h = `${formatTime.hh}`;
			datas.m = `${formatTime.mm}`;
			datas.s = `${formatTime.ss}`;
			datas.times = datas.times / 1000;
			that.pintuanData = datas;
			// console.log(datas.s);
			if (that.countDown == 2) {
				setTimeout(this.setCountDown, time);
			}

		},

	}
}
